@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: 300;
  src: url("Roboto-Thin.2eb18318.ttf");
  font-display: optional;
}

@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: 500;
  src: url("Roboto-Regular.5068caf2.ttf");
  font-display: optional;
}

@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: "bold";
  src: url("Roboto-Bold.61bf53a1.ttf");
  font-display: optional;
}

@font-face {
  font-family: Cantarell;
  font-style: "normal";
  font-weight: "bold";
  src: url("Cantarell-Bold.ac5c3783.ttf");
  font-display: optional;
}

@font-face {
  font-family: Cantarell;
  font-style: "normal";
  src: url("Cantarell-Regular.d9697145.ttf");
  font-display: optional;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #010400;
  background: #fcfcfc;
  font-family: Roboto;
  font-size: 14px;
}

a {
  color: #010400;
  text-decoration: none;
}

h1, h2, h3 {
  font-family: Roboto, sans-serif;
}

.content, .top-nav {
  max-width: 70%;
  margin: 0 auto;
  padding: 0 124px;
}

@media (max-width: 840px) {
  .content, .top-nav {
    max-width: 100%;
    padding: 0 8%;
  }
}

@media (max-width: 480px) {
  .content, .top-nav {
    max-width: 100%;
    padding: 0 4%;
  }
}

.top-nav {
  height: 90px;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.top-nav ul {
  align-items: center;
  padding: 2em 0;
  list-style-type: none;
  display: flex;
}

.top-nav li {
  flex-grow: 1;
  padding-right: 2em;
}

.top-nav li a {
  color: #010400;
  font-family: Roboto, sans-serif;
  font-size: 1.4em;
  text-decoration: underline;
}

.top-nav li a:hover {
  color: #465c69;
}

.top-nav li:last-child {
  padding: 0;
}

@media (max-width: 480px) {
  .top-nav li {
    padding-right: 1em;
  }

  .top-nav li a {
    font-size: 1.2em;
  }
}

.top-nav .brand {
  width: 60px;
  height: 60px;
  flex-grow: 1;
  align-self: center;
}

.top-nav .brand-avatar {
  height: 100%;
}

.title {
  margin: 2em 0;
}

.title h1 {
  text-transform: capitalize;
  font-family: Roboto, sans-serif;
  font-size: 1.7em;
  font-weight: bold;
  line-height: 1.2;
}

blockquote {
  background-color: #cfd9df;
  border-left: 4px solid #465c69;
  margin: 1em 0;
  padding: .01em 1em;
}

img figcaption, figure figcaption {
  border-bottom: 2px solid #465c69;
  padding: 0 .4em .4em;
  font-size: .7em;
}

pre {
  padding: 1em;
  overflow: auto;
}

#postamble {
  color: #666;
  margin: 4rem 0;
  font-family: monospace;
}

#postamble .validation {
  display: none;
}

.generic-listing {
  font-family: Cantarell, sans-serif;
  list-style-type: none;
}

.generic-li {
  align-items: center;
  margin-bottom: .6em;
  padding: .3em 0;
  line-height: 1.3;
  display: flex;
}

.li-icon {
  content: "";
  width: 88px;
  height: 48px;
  background-position: 0 0;
  background-repeat: no-repeat;
  flex-shrink: 0;
  display: block;
}

.li-icon--blog {
  background-image: url("post.6dd5fefd.svg");
}

.li-icon--talks {
  background-image: url("talk.7e2d06c8.svg");
}

.li-icon--poems {
  background-image: url("poems.21b7f1bb.svg");
  background-size: contain;
}

.generic-li-title {
  font-size: 1.2em;
}

.generic-li-meta {
  margin-top: .5em;
  font-size: .6em;
}

.generic-li-meta, .generic-li-meta a {
  color: #465c69;
  font-family: Roboto, sans-serif;
  display: flex;
}

.generic-li-meta .meta-item, .generic-li-meta a .meta-item {
  padding: 0 1em 0 0;
  line-height: 1;
  display: flex;
}

.generic-li-meta .meta-item:last-child, .generic-li-meta a .meta-item:last-child {
  border-right: none;
  padding-right: 0;
}

.generic-li-meta .tags, .generic-li-meta a .tags {
  text-transform: capitalize;
  flex-wrap: wrap;
  display: flex;
}

.generic-li-meta .tags a, .generic-li-meta a .tags a {
  margin-left: 1em;
}

.content-meta {
  margin-top: .5em;
}

.content-meta, .content-meta a {
  color: #465c69;
  font-family: Roboto, sans-serif;
  display: flex;
}

.content-meta .meta-item, .content-meta a .meta-item {
  padding: 0 1em;
  font-size: .8em;
  line-height: 1;
  display: flex;
}

.content-meta .meta-item:last-child, .content-meta a .meta-item:last-child {
  border: none;
  padding-right: 0;
}

.content-meta .date, .content-meta a .date {
  padding: 0 1em 0 0;
}

.content-meta .tags ul, .content-meta a .tags ul {
  flex-wrap: wrap;
  list-style-type: none;
  display: flex;
}

.content-meta .tags ul li, .content-meta a .tags ul li {
  padding-right: 1em;
}

.content {
  min-height: 500px;
  font-family: Cantarell, sans-serif;
  font-size: 1.5em;
  font-style: normal;
  font-weight: 500;
  line-height: 1.4;
}

.content strong, .content b {
  font-weight: bold;
}

.content p {
  margin: 1em 0;
}

.content h3 {
  margin: .4em 0;
}

.content a {
  text-decoration: underline;
}

.content ul {
  margin-left: 1.4em;
}

.content li {
  margin: .7em 0 .7em .7em;
}

.content-footer {
  flex-direction: column;
  justify-content: space-between;
  margin: 6em 0;
  font-family: Roboto, monospace;
  display: flex;
}

.content-footer > nav {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.content-footer > .post-comments {
  margin-top: 3em;
}

.footer-nav {
  width: 40%;
  font-weight: bold;
}

.footer-nav label {
  color: #465c69;
  font-size: .7em;
  display: block;
}

.footer-nav a {
  line-height: 1.4;
  text-decoration: underline;
  display: block;
}

.home {
  display: flex;
}

@media (max-width: 840px) {
  .home {
    flex-direction: column;
  }
}

.home .sidebar {
  width: 450px;
  text-align: center;
  border-right: .2px solid #6f6f6fd1;
  padding: 35px;
  position: relative;
  top: 0%;
  bottom: 0%;
  left: 0;
}

.home .sidebar .name {
  font-family: Roboto;
  font-size: 2em;
  font-weight: bold;
  line-height: 2.6;
}

.home .sidebar .handle {
  font-family: Roboto;
  font-size: 1.3em;
  font-weight: 300;
  line-height: 0;
}

.home .sidebar .quote {
  margin: 45px 0 15px;
  padding: 20px;
  font-family: Roboto;
  font-size: 1.4em;
  font-weight: 300;
  position: relative;
}

.home .sidebar .quote:before {
  content: "";
  width: 14px;
  height: 14px;
  background: url("quote.3fa3b0e1.svg") no-repeat;
  position: absolute;
  top: 0;
  left: 21px;
}

.home .sidebar .social {
  width: 148px;
  margin: 0 auto;
  padding-top: 40px;
  position: relative;
  overflow: hidden;
}

.home .sidebar .social a {
  clear: top;
  float: left;
  width: calc(50% - 20px);
  margin: 10px;
  display: block;
}

.home .sidebar .social span {
  width: 48px;
  height: 48px;
  display: block;
  position: relative;
}

.home .sidebar .social .github {
  background: url("github.d110c599.svg") no-repeat;
}

.home .sidebar .social .stackoverflow {
  background: url("stackoverflow.590d40a2.svg") no-repeat;
}

.home .sidebar .social .rss {
  background: url("rss.92cbf38e.svg") no-repeat;
}

.home .sidebar .social .linkedin {
  background: url("linkedin.90b7fb3e.svg") no-repeat;
}

.home .sidebar .pub-key-qr {
  margin: 55px auto 0;
}

@media (max-width: 990px) {
  .home .sidebar {
    width: 40%;
  }
}

@media (max-width: 840px) {
  .home .sidebar {
    width: 100%;
  }
}

.home .main {
  width: calc(100% - 450px);
  max-width: 872px;
  padding: 40px;
  position: relative;
}

.home .main h2 {
  padding: 10px 0;
  font-family: Roboto, sans-serif;
  font-size: 2.2em;
  font-weight: bold;
}

.home .main .about-me-snippet p {
  align-items: center;
  margin: .8em 0;
  font-family: Cantarell, sans-serif;
  font-size: 1.35em;
  font-weight: 500;
  line-height: 1.5;
}

.home .main .about-me-snippet p a {
  font-weight: bold;
  text-decoration: underline;
}

.home .main .about-me-snippet p a:active {
  color: #465c69;
}

.home .main .hire-status {
  width: 14em;
  height: 2em;
  text-align: center;
  margin: 1em 0 2.4em;
  padding: .2em .4em;
  font-size: 1.4em;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.home .main .hire-status__title {
  text-align: center;
  width: 100%;
  padding: .2em;
  line-height: 1;
}

.home .main .hire-status__dialog {
  opacity: 0;
  text-align: left;
  padding: 10px 15px;
  line-height: 1.7;
  transition: opacity;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.home .main .hire-status__dialog__title {
  padding: 0;
  font-size: 1.5em;
}

@media (max-width: 480px) {
  .home .main .hire-status {
    width: 100%;
  }
}

.home .main .hire-status--expanded {
  width: 25em;
  height: 11em;
}

.home .main .hire-status--expanded .hire-status__title {
  display: none;
}

.home .main .hire-status--expanded .hire-status__dialog {
  opacity: 1;
  flex-flow: column;
  transition: opacity 1s ease-out;
}

@media (max-width: 480px) {
  .home .main .hire-status--expanded .hire-status__dialog {
    position: relative;
  }
}

@media (max-width: 480px) {
  .home .main .hire-status--expanded {
    width: 100%;
    height: 100%;
  }
}

.home .main .recent-content {
  font-family: Cantarell, sans-serif;
}

.home .main .recent-content .recent-content-list {
  margin: 2em 0;
  list-style-type: none;
}

.home .main .recent-content .recent-content-list li {
  margin: 0;
  padding: 0;
}

.home .main .recent-content .recent-content-item {
  align-items: center;
  margin-bottom: .4em;
  padding: .3em 0;
  font-size: 1.6em;
  line-height: 1.4;
  display: flex;
}

.home .main .recent-content .recent-content-item:before {
  content: "";
  width: 88px;
  height: 48px;
  background-position: 0 0;
  background-repeat: no-repeat;
  flex-shrink: 0;
  display: block;
}

.home .main .recent-content .content-type--blog:before {
  background-image: url("post.6dd5fefd.svg");
}

.home .main .recent-content .content-type--talks:before {
  background-image: url("talk.7e2d06c8.svg");
}

.home .main .recent-content .content-type--poems:before {
  background-image: url("poems.21b7f1bb.svg");
  background-size: contain;
}

.home .main .recent-content .read-more-btn {
  margin: .7em 0;
  font-size: 1.3em;
}

@media (max-width: 990px) {
  .home .main {
    width: 60%;
  }
}

@media (max-width: 840px) {
  .home .main {
    width: 100%;
  }
}

.btn {
  cursor: pointer;
  text-align: center;
  border: 2px solid ;
  border-radius: 25px;
  align-items: center;
  padding: 0 2.1em;
  font-family: Roboto;
  font-weight: 500;
  line-height: 2.2em;
  text-decoration: none;
  display: inline-flex;
}

.btn-cta {
  color: #fcfcfc;
  background: #b75d69;
  border-color: #b75d69;
}

.btn-cta:hover {
  background: #b55966;
}

.btn-cta:active {
  background: #ad4d5a;
}

.btn-primary {
  color: #006992;
  background: #fcfcfc;
  border-color: #006992;
}

.btn-primary:hover {
  background: #f9f9f9;
}

.btn-primary:active {
  background: #efefef;
}

.btn-secondary {
  color: #465c69;
  background: #fcfcfc;
  border-color: #465c69;
}

.btn-secondary:hover {
  background: #f9f9f9;
}

.btn-secondary:active {
  background: #efefef;
}

